#root {
  height: 100%;
  width: 100%;
  padding: 0;
  margin: 0;
}

#container {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-rows: auto;
  grid-template-columns: 1fr 10px 9999fr;
  justify-items: start;
  -webkit-transition: all 0.8s linear;
  transition: all 0.8s linear;
}

#content {
  width: 100%;
  height: 100vh;
  margin: 0 auto;
  overflow-x: auto;
}

#page {
  display: flex;
  flex-direction: column;
  padding: 0 5% 24px;
  float: left; /* required for the padding-right to work properly */
  -webkit-animation: fadein 400ms;
          animation: fadein 400ms;
}
#wide-page {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  -webkit-animation: fadein 400ms;
          animation: fadein 400ms;
  overflow-x: hidden;
}
@-webkit-keyframes fadein {
  from { opacity: 0; }
  to   { opacity: 1; }
}
@keyframes fadein {
  from { opacity: 0; }
  to   { opacity: 1; }
}

#settings {
  min-height: 80px;
  display: flex;
  align-items: center;
}

#settings input {
  margin-left: 3em;
}

.thread-details {
  width: 98%;
  height: 98%;
  margin: auto;
  display: flex;
  flex-direction: column;
  align-content: center;
}

.stacktrace-window {
  width: 100%;
  height: 100%;
  color: #6B778C;
  padding: 5px;
  font-size: 12px;
  line-height: 16px;
  margin-top: 8px;
  resize: none;
}

#consumers-list {
  padding: 0;
  white-space: pre;
  list-style: none;
}
#consumers-list h6 {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.cpu-usage:hover {
  cursor: pointer;
  color: #172B4D;
  text-decoration: underline;
}
.high {
  color: #BF2600;
}
.mid {
  color: #403294;
}
.low {
  color: #008DA6;
}
.vlow {
  color: #006644;
}

.monitors-container {
  display: flex;
}
.monitors-container div {
  padding: 6px;

  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.monitors-container .left {
  width: 360px;
  min-width: 360px;
}

.filters label {
  font-weight: 500;
  margin-left: 8px;
  padding: 7px 10px;
  border: 1px solid transparent;
  border-radius: 3px;
  color: #0052CC;
}
.filters label:hover {
  border: 1px solid #C1C7D0;
  color: #0065FF;
  text-decoration: none;
}

.filters .checked {
  color: #FFF;
  background: #172B4D;
}
.filters .checked:hover {
  border: 1px solid transparent;
  color: #091E42;
  background: #EBECF0;
}

.filters input {
  display: none;
}

nav {
  width: 0;
  color: #172B4D;
  background: #F4F5F7;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}
nav.open {
  width: 230px;
}

#brand-visible {
  color: #0052CC;
  padding-left: 30px;
  -webkit-transform: scale(1);
          transform: scale(1);
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}
#brand-hidden {
  -webkit-transform: scale(0);
          transform: scale(0);
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}

nav ul {
  list-style-type: none;
  padding: 0;
  margin-bottom: 20px;
}
nav li {
  -webkit-transform: scale(0);
          transform: scale(0);
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}
nav.open li {
  width: 200px;
  font-size: 14px;
  line-height: 36px;
  padding-left: 30px;
  -webkit-transform: scale(1);
          transform: scale(1);
}
nav.open li:hover {
  cursor: pointer;
  color: #0052CC;
  background: #EBECF0;
}

nav a:hover {
  text-decoration: none;
}

#nav-content-bottom {
  bottom: 0;
  position: fixed;
}

.nav-toggle {
  width: 10px;
  background: #fff;
  height: 100%;
}
#nav-arrow {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

/* vertical line */
.nav-toggle:hover {
  background: -webkit-gradient(linear, left top, right top, color-stop(30%, #0052CC), color-stop(30%, #fff));
  background: linear-gradient(90deg, #0052CC 30%, #fff 30%);
  cursor: pointer;
}

/* arrow */
#nav-arrow:before,
#nav-arrow:after {
  height: 100vh;
  background: #0052CC;
  content: '';
  height: 3px;
  position: absolute;
  width: 9px;
}

/* top part of the arrow */
#nav-arrow:before {
  -webkit-transform: translate(1px, 1px) rotate(55deg);
          transform: translate(1px, 1px) rotate(55deg);
  -webkit-transform-origin: right;
          transform-origin: right;
}
.open #nav-arrow:after {
  -webkit-transform: translate(5px, 1px) rotate(-55deg);
          transform: translate(5px, 1px) rotate(-55deg);
  -webkit-transform-origin: left;
          transform-origin: left;
}

/* bottom part of the arrow  */
#nav-arrow:after {
  -webkit-transform: translateX(1px) rotate(-55deg);
          transform: translateX(1px) rotate(-55deg);
  -webkit-transform-origin: right;
          transform-origin: right;
}
.open #nav-arrow:before {
  -webkit-transform: translateX(5px) rotate(55deg);
          transform: translateX(5px) rotate(55deg);
  -webkit-transform-origin: left;
          transform-origin: left;
}

.stacktrace {
  font-family: "SF Mono", "Segoe UI Mono", "Roboto Mono", "Ubuntu Mono", Menlo, Courier, monospace;
  font-size: 12px;
  line-height: 16px;
  margin-top: 0;
}

#memory-usages {
  width: 80%;
  min-height: 300px;
  display: flex;
}
#memory-usages > div {
  width: 50%;
  text-align: center;
}

.chart {
  width: 80%;
}

#threads-overview-settings {
  margin-top: 22px;
  line-height: 36px;
  min-height: 36px;
  width: 90%;
  display: flex;
  align-items: center;
}
#regexp-filters label {
  margin-left: 3em;
}
@media (max-width: 1666px) {
  #regexp-filters label {
    margin-left: 16px;
  }
  #regexp-filters input {
    width: 100px;
  }
}
@media (max-width: 1439px) {
  #regexp-filters label {
    display: block;
  }
}
@media (max-width: 1124px) {
  #regexp-filters label {
    display: none;
  }
}

#matching-summary {
  width: 90%;
  min-height: 20px;
}

#thread-overview-legend {
  height: 32px;
  width: 90%;
  table-layout: fixed;
  text-align: center;
}

table {
  position: relative;
  width: 99%;
  padding-left: 4px;
  margin: 10px 0 4px;
  border-spacing: 2px;
  table-layout: fixed;
}
td {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  color: #6B778C;
  cursor: pointer;
}
td.name {
  font-weight: 700;
  cursor: default;
}
td.empty {
  background: #FFF;
  cursor: default;
}
td.runnable {
  background: #ABF5D1;
}
td.blocked {
  background: #FFBDAD;
}
td.waiting {
  background: #EAE6FF;
}
td.timed-waiting {
  background: #FFFAE6;
}
td.unknown {
  background: #FFF;
}
td.matching {
  background: #B3D4FF;
}

td > span,
th > span {
  visibility: hidden;
  position: absolute;
  z-index: 100;
  opacity: 0;
  -webkit-transition: opacity 0.3s ease 0.3s;
  transition: opacity 0.3s ease 0.3s;
  border-radius: 3px;
  background-color: #172B4D;
  color: #fff;
  font-weight: bold;
  padding: 2px 7px 4px;
}
td:hover > span,
th:hover > span {
  display: block;
  visibility: visible;
  opacity: 1;
}

#dropzone {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

#dropzone h4 {
  width: 600px;
  height: 200px;
  line-height: 200px;
  text-align: center;

  background: #f4f5f7;
  border-radius: 5px;
  border: 2px dashed #0052CC;
}

#dropzone-links {
  padding: 0;
  margin: 0;
}
#dropzone-links li {
  padding: 4px;
  margin: 0 6px;
  display: inline-block;
}

html, body {
  height: 100%;
}

body {
  margin: 0;
  background: #fff;
  color: #6B778C;
  font-size: 14px;
  font-weight: 400;
  letter-spacing: -0.005em;
  line-height: 20px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Noto Sans", "Ubuntu", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.mono {
  font-family: "SF Mono", "Segoe UI Mono", "Roboto Mono", "Ubuntu Mono", Menlo, Courier, monospace;
}

h1 {
  font-size: 35px;
  font-weight: 500;
  letter-spacing: -0.01em;
  line-height: 40px;
}

h2 {
  color: #172B4D;
  font-size: 29px;
  font-weight: 500;
  letter-spacing: -0.01em;
  line-height: 32px;
  margin-top: 40px;
}

h4 {
  color: #172B4D;
  font-size: 20px;
  font-weight: 500;
  letter-spacing: -0.008em;
  line-height: 24px;
}

h6 {
  color: #172B4D;
  font-size: 14px;
  font-weight: 600;
  letter-spacing: -0.003em;
  line-height: 16px;
  margin-top: 16px;
  margin-bottom: 0;
}

a {
  cursor: pointer;
  color: #172B4D;
  text-decoration: none;
}
a:hover {
  color: #0052CC;
  text-decoration: underline;
}

input {
  color: #172B4D;
  border: 1px solid #c1c7d0;
  border-radius: 3.01px;
  box-sizing: border-box;
  font-size: inherit;
  margin: 0;
  vertical-align: baseline;
  padding: 4px 5px;
  margin-right: 4px;
}
input[type="number"], input[type="text"] {
  height: 2.14285714em;
  line-height: 1.4285714285714;
}
input[type="number"] {
  width: 5em;
}
input:focus {
  border-color: #5897fb;
}

.chevron {
  width: 14px;
  height: 14px;
  display: inline-block;
  position: relative;
  top: 2px;
  color: #344563;
  background: rgba(9,30,66,.08) url(/chevron.svg) no-repeat top left;
  border-radius: 3.01px;
  margin-right: 5px;
  -webkit-transition: background-color .1s ease-out;
  transition: background-color .1s ease-out;
}
.rotate {
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
}

.clickable {
  cursor: pointer;
}

#kudos {
  -webkit-animation: colorchange 20s infinite;
          animation: colorchange 20s infinite;
}
@-webkit-keyframes colorchange {
  0% {
    color: #0747A6;
  }
  20% {
    color: #008DA6;
  }
  40% {
    color: #006644;
  }
  60% {
    color: #BF2600;
  }
  80% {
    color: #403294;
  }
  100% {
    color: #0747A6;
  }
}
@keyframes colorchange {
  0% {
    color: #0747A6;
  }
  20% {
    color: #008DA6;
  }
  40% {
    color: #006644;
  }
  60% {
    color: #BF2600;
  }
  80% {
    color: #403294;
  }
  100% {
    color: #0747A6;
  }
}

#centered {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

